@keyframes loadingSpinAround {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(359deg);
  }
}

@media screen and (width <= 780px) {
  .heatbox-header .logo-container .title, .heatbox-header .logo-container .subtitle {
    width: 100%;
  }
}

.weed-fields {
  position: relative;
}

.weed-field {
  margin-bottom: 15px;
}

.weed-field .wp-picker-input-wrap {
  max-width: 220px;
  transition: max-width .2s;
  display: inline-flex;
  overflow: hidden;
}

.weed-field .wp-picker-input-wrap .color-picker {
  width: 160px !important;
}

.weed-field .wp-picker-input-wrap.hidden {
  max-width: 0;
  display: inline-flex;
}

.weed-inline-fields {
  display: flex;
  position: relative;
}

.weed-inline-fields .weed-field {
  margin-right: 10px;
}

.weed-inline-fields .weed-field:last-child {
  margin-bottom: 0;
  margin-right: 0;
}

.weed-label {
  margin-bottom: 3px;
  display: block;
}

.is-hidden {
  display: none;
}

.button.weed-test-email-button {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.weed-test-email-button.is-loading {
  pointer-events: none;
  color: #0000 !important;
}

.weed-test-email-button.is-loading:after {
  content: "";
  border-style: solid;
  border-width: 2px;
  border-radius: 290486px;
  width: 1em;
  height: 1em;
  animation: .5s linear infinite loadingSpinAround;
  display: block;
  position: absolute;
  border-color: #0000 #0000 #fff #fff !important;
}

.button.weed-reset-button {
  color: #fff;
  background-color: tomato;
  border-color: tomato;
  margin-left: 7px;
}

.button.weed-reset-button:hover, .button.weed-reset-button:active {
  color: #fff;
  background-color: #f5492a;
  border-color: #f5492a;
}

.button.weed-reset-button:focus {
  color: #fff;
  background-color: #f5492a;
  border-color: #f5492a;
  box-shadow: 0 0 0 1px #fff, 0 0 0 3px #f5492a;
}

.CodeMirror-wrap {
  border: 1px solid #ddd;
}

.weed-tags-metabox .tags-wrapper {
  line-height: 1.8;
}

.weed-tags-metabox h2 {
  position: relative;
}

.weed-tags-metabox h2 .action-status {
  color: #fff;
  opacity: 0;
  visibility: hidden;
  background-color: #444;
  border-radius: 4px;
  height: 30px;
  padding: 10px;
  font-size: 11px;
  line-height: 10px;
  transition: all .3s ease-in-out;
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.weed-tags-metabox h2 .action-status.is-shown {
  opacity: 1;
  visibility: visible;
}

.weed-tags-metabox .heatbox-content p:last-child {
  margin-bottom: 0;
}

.weed-featured-products > h2 {
  text-align: center;
  margin-bottom: 50px;
  font-size: 24px;
}

.weed-featured-products .products {
  justify-content: space-between;
  display: flex;
}

.weed-featured-products .products li {
  width: 32%;
}

.weed-featured-products .products .subheadline {
  margin-top: -6px;
  font-weight: 600;
}

.weed-featured-products .products img {
  max-width: 100%;
}

.weed-featured-products .credit {
  text-align: center;
  opacity: .5;
  margin-top: 20px;
}

